.portfolio {
	// background: #F5F5F5;
	background: $silver-blue;

		.pieces {
			width: 90%;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: auto;
		}
}

.project-copy {
	margin-left: 3%;
	width: 50%;
	display: flex;
	flex-direction: column;

	h4 {
		padding-bottom: 3%;
		border-bottom: 7px solid $dark-accent;
	}

	.skills p {
		color: $newsprint;
	}
}


.pieces:nth-child(even) {
	flex-direction: row-reverse;
	margin: 5%;

	.project-copy {
		margin-left: 0;
		margin-right: 3%;
	}
}

.pieces:last-child {
	margin-bottom: 0;
}

.live {
	width: 30%;
	margin: auto;
	padding: 2%;
	border: none;
	color: $mauve;
	background: $newsprint;
	font-size: 1.5rem;
	transition: all 0.3s linear;
	border-radius: 5px;
	&:hover,
	&:focus {
		background: $mauve;
		color: $newsprint;
	}
}